import { SignUp } from './SignUp'
import { Link } from 'react-router-dom'

import './Form.css'

function RegisterPage() {
  return (
    <div className="helloPage-wrapper">
      <div className="helloPage">
        <h1></h1>
        <SignUp />
        <p>
              ? <Link to="/login"></Link>
        </p>
      </div>
    </div>
  )
}

export default RegisterPage
